.body{
  justify-content: center; 
}
ol, ul {
  padding-left: 30px;
}

.containerDEAD{
  text-align: center;
  /* text-decoration: dotted; */
  /* text-shadow: #61dafb; */
  align-content: center;
}

.board{
  align-content: center;
  display: inline-flex;
  flex-wrap: wrap; 
  width: 276px; /* 90*3+(1+2+2+1) //border */
}

.boardRow{
  display:table;
  align-content: center;
  justify-content: center;
} 
.boardRow:after {
  clear: both;
  content: "";
  display: table;
}

.game {
  display: flex;
  flex-direction: row;
}

.square {
  background: cornsilk;
  border: 1px solid #999;
  float: left;
  font-size: 40px;
  font-weight: bold;
  line-height: 34px;
  height: 90px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 90px;
}

.squareDEAD{
  width: 90px;
  height: 90px;
  /* border-top: 1px; */
  border: 1px solid;
  font-size: 40px;
  /* border-width: 2px; */
  display: inline-block;
  background:cornsilk;
  
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}


/* ------ prototype content below ------ */
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
